<style>
  .store-check-wraper {
    border: 1px solid #ececec;
    border-radius: 8px;
    padding: 8px;
    width: 530px;
    box-sizing: border-box;
  }
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .del-store-icon-w {
    position: absolute;
    top: -8px;
    right: 2px;
    cursor: pointer;
  }
</style>
<template>
  <div>
    <div class="add-form-wraper">
      <el-form ref="form" :model="addParamMesg" label-width="140px">
        <el-form-item label="活动类型">
          <p style="color: #222;font-size: 16px; font-weight: bold;">{{addParamMesg.activityTypeName}}</p>
        </el-form-item>
        <el-form-item label="选择区域">
          <p>{{addParamMesg.regionName}}</p>
        </el-form-item>
        <el-form-item label="活动编码">
          <p>{{addParamMesg.activityCode}}</p>
        </el-form-item>
        <el-form-item label="活动名称">
          <p>{{addParamMesg.activityName}}</p>
        </el-form-item>
        <el-form-item label="开始时间">
          {{addParamMesg.activityStartDate}}
        </el-form-item>
        <el-form-item label="结束时间">
          {{addParamMesg.activityEndDate}}
        </el-form-item>
        <el-form-item label="配送方式">
          <div>
            <el-checkbox v-model="canDelivery" disabled>支持配送</el-checkbox>
            <el-radio-group v-model="deliveryTypeTemp" v-if="canDelivery" style="margin-left: 30px;" disabled>
              <el-radio :label="1">支持外卖配送</el-radio>
              <el-radio :label="2">支持快递配送</el-radio>
            </el-radio-group>
          </div>
          <div>
            <el-checkbox v-model="canPickSelf" disabled>支持自提</el-checkbox>
          </div>
        </el-form-item>
        <el-form-item label="参与门店">
          <div v-if="activityStoresCheckedList.length > 0" class="store-check-wraper">
            <el-table
              class="fixed-height"
              :data="activityStoresCheckedList"
              max-height="300"
              size="small">
              <el-table-column min-width="226px" label="门店编码/名称">
                <template slot-scope="scope">
                  <div>
                    {{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="100" prop="saleTypeName" label="门店类型"></el-table-column>
            </el-table>
          </div>
        </el-form-item>
        <el-form-item label="参与商品">
          <div v-if="activityGoodsCheckedList.length > 0">
            <el-table
              class="fixed-height"
              :data="activityGoodsCheckedList"
              empty-text="暂无选中任何商品"
              style="width:100%;"
              max-height="400"
              size="small">
              <el-table-column min-width="220px" label="商品信息">
                <template slot-scope="scope">
                  <div class="ll-p-m-w">
                    <div class="ll-img-w">
                      <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                      <img v-else src="/shop/static/img_error.png" alt="">
                    </div>
                    <div class="ll-p-m-c-r">
                      <div>
                        {{scope.row.goodsName || '-'}}
                      </div>
                      <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                      <div class="ll-erp-p-no">商品ID：{{scope.row.id}}</div>
                    </div>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
              <el-table-column min-width="130px" prop="activityGoodsName" label="活动商品名称"></el-table-column>
              <el-table-column min-width="110px" prop="limitGroupType" label="拼团限制" :formatter="limitGroupTypeFormater"></el-table-column>
              <el-table-column min-width="90px" prop="price" label="零售价"></el-table-column>
              <el-table-column min-width="110px" prop="groupPrice" label="拼团价"></el-table-column>
              <el-table-column min-width="110px" prop="groupCount" label="成团人数"></el-table-column>
              <el-table-column min-width="150px" prop="limitGoodsCount" label="限购数量(为空不限)"></el-table-column>
              <el-table-column min-width="110px" prop="curStock" label="拼团库存"></el-table-column>
              <el-table-column min-width="120px" prop="activityTimes" label="成团时长(分钟)"></el-table-column>
              <el-table-column min-width="110px" prop="sortNum" label="排序权重"></el-table-column>
            </el-table>
            <div>
              <span style="color: red;">*</span>注意：权重排序，先创建的活动商品在前；剩余的按前端规则走。
            </div>
          </div>
        </el-form-item>

        <hr class="s-slip-tb">
        <el-form-item>
          <el-button size="small" @click="doCancelHandel">返回</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 活动规则编辑跳转
    edit: "/plateform/a/activityRule/edit",
    // 新增活动规则
    add: "/plateform/a/activityRule/add"
  }
  export default {
    data() {
      return {
        isDisabled: false,
        addParamMesg: {
          regionCode: '',
          regionName: '',
          activityId: '', // 活动id
          activityType: '', // 活动类型
          activityTypeName: '', // 活动类型名称
          activityName: '', // 活动名称
          activityStartDate: '', // 活动开始时间
          activityEndDate: '', // 活动结束时间
          activityCode: '', // 活动编码
          // 参与门店
          activityStoreList: [],
          // 活动商品集合
          platCityGoodsInfos: [],
          deliveryType: 0, // 0 不支持配送 1  支持外卖配送   2  支持快递配送
          pickSelf: 0 // 0 不支持自提  1 支持自提
        },
        // 参与门店选中的
        activityStoresCheckedList: [],
        // 参与的选中的商品
        activityGoodsCheckedList: [],
        // 当前选中的商品用于批量设置价格
        checkedList: [],
        // 批量修改折扣
        discount: '',
        // 支持配送
        canDelivery: false,
        deliveryTypeTemp: 0, // 1 支持外卖配送   2 支持自提配送
        // 支持自提
        canPickSelf: false


      }
    },
    created() {
      let activityId = this.$route.params.activityId
      this.addParamMesg.activityId = activityId
      this.getDetail()
    },
    mounted() {

    },
    methods: {
      /**
       * [getDetail 获取拼团活动详情]
       * @return {[type]} [description]
       */
      getDetail() {
        let activityId = this.addParamMesg.activityId || ''
        utils.showLoading()
        this.$http.post(reqUrl.edit, {activityId: activityId}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              let createGoodsMesg = res.data
              this.addParamMesg.activityId = createGoodsMesg.id
              this.addParamMesg.regionCode = createGoodsMesg.regionCode || ''
              this.addParamMesg.regionName = createGoodsMesg.regionName || ''
              this.addParamMesg.activityType = createGoodsMesg.activityType || ''
              this.addParamMesg.activityTypeName = createGoodsMesg.activityTypeName || ''
              this.addParamMesg.activityName = createGoodsMesg.activityName || ''
              this.addParamMesg.activityStartDate = createGoodsMesg.activityStartDate || ''
              this.addParamMesg.activityEndDate = createGoodsMesg.activityEndDate || ''
              this.addParamMesg.activityCode = createGoodsMesg.activityCode || ''
              this.addParamMesg.limitDelivery = createGoodsMesg.limitDelivery ? createGoodsMesg.limitDelivery : 0
              if(createGoodsMesg.pickSelf === 1) {
                this.canPickSelf = true
              }else {
                this.canPickSelf = false
              }
              if(createGoodsMesg.deliveryType === 0) {
                this.canDelivery = false
                this.deliveryTypeTemp = 1
              }else if(createGoodsMesg.deliveryType === 1) {
                this.canDelivery = true
                this.deliveryTypeTemp = 1
              }else if(createGoodsMesg.deliveryType === 2) {
                this.canDelivery = true
                this.deliveryTypeTemp = 2
              }
              if(createGoodsMesg.storeVos && createGoodsMesg.storeVos.length) {
                this.activityStoresCheckedList = createGoodsMesg.storeVos
              }
              if(createGoodsMesg.platCityGoodsVos && createGoodsMesg.platCityGoodsVos.length) {
                for(let item of createGoodsMesg.platCityGoodsVos) {
                  item.copyUrl = `pages/product/ptdetail?id=${item.platformCityGoodsId}&type=5`
                }
                this.activityGoodsCheckedList = createGoodsMesg.platCityGoodsVos
              }
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      limitGroupTypeFormater(row, column, cellValue) {
        if(cellValue === 0) {
          return "全部"
        }else if(cellValue === 1) {
          return "新会员"
        }else if(cellValue === 2) {
          return "老会员"
        }
      },
      /**
       * [copyUrl 复制链接成功]
       * @return {[type]}      [description]
       */
      onCopySuc() {
        this.$message.success("链接已复制至剪切板，仅限微信公众号中使用")
      },
      /**
       * [onCopyError 复制链接失败]
       * @return {[type]} [description]
       */
      onCopyError() {
        this.$message.error("复制链接失败,请手动复制")
      },
      /**
       * [doCancelHandel 返回]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$router.push('/store_act')
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }

</script>



